<!-- 侧滑菜单 组件 -->
<template>
  <div>
    <transition name="fold">
      <div class="sidebar" v-show="sidebarShow">
        <div v-for="(item) in getCarList" :key="item.SerialID" class="carList" @click="toCarInfo(item.SerialID)">
          <van-image lazy-load width="150" height="100" :src="item.Picture">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <div class="title">
            <p class="t">{{item.AliasName}}</p>
            <p class="xilie">产地：{{item.Country}}</p>
            <p class="price">{{item.DealerPrice}}</p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div class="mask" @click.stop.prevent="hide" v-show="sidebarShow"></div>
    </transition>
  </div>
</template>
 
<script type="text/ecmascript-6">
import * as api from "../../api/index.js";
export default {
  //接收父组件传值
  props: {
    sidebarShow: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: "", //传进来的id
      carList: []
    };
  },
  //生命周期创建观察数据
  created() {
    let _self = this;
    _self.$eventBus.$on("car", function(val) {
      _self.id = val;
    });
  },
  //观察路由跳转更新数据
  watch: {
    id(newVal) {
      this.getsidebarInfo(this.id);
    }
  },
  methods: {
    //隐藏侧边栏,向上派发事件(向父组件传值)
    hide() {
      this.$emit("hideSidebar", false);
    },
    async getsidebarInfo(id) {
      let res = await api.getSidebarInfo(id);
      this.carList = res;
    },
    toCarInfo(id){
      this.$router.push({path:"/carinfo",query:{SerialID:id}})
    }
  },
  computed: {
    getCarList() {
      let arr = this.carList;
      for (var i = 0; i < arr.length; i++) {
        //循环数组
        var obj = arr[i]; //得到的是数组中的每个对象
        for (var key in obj) {
          //遍历对象
          if (key == "Picture") {
            let data = obj[key];
            obj[key] = data.replace(/\{0\}/g, "6");
          }
        }
      }
      return arr;
    }
  }
};
</script>
 
<style lang="scss">
.sidebar {
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 50;
  height: 100%;
  width: 80%;
  overflow-y: auto;
  overflow-x: hidden;
  word-wrap: break-word;
  // background-color: rgb(35, 42, 48);
  background-color: #fff;
  transform: translate3d(0, 0, 0);
  .carList {
    box-sizing: border-box;
    margin: 0 10px;
    border-bottom: 1px solid #eee;
    display: flex;
    .title {
      margin-top: 15px;
      .t {
        padding: 0;
        font-size: 14px;
        line-height: 20px;
      }
      .xilie {
        margin-top: 4px;
        font-size: 12px;
        color: #999;
      }
      .price {
        display: inline-block;
        color: red;
        font-size: 9pt;
        line-height: 18px;
      }
    }
  }
}
.mask {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7, 17, 27, 0.6);
  opacity: 1;
  &.fade-enter-active,
  &.fade-leave-active {
    transition: all 0.5s;
  }
  &.fade-enter,
  &.fade-leave-active {
    opacity: 0;
  }
}
</style>