<script>
import msidebar from "./sidebar"
export default {
    props:{carList:Array},
    data() {
        return {
            indexList:["A","B","C","D","F","G","H","J","K","L","M","N","O","Q","R","S","T","W","X","Y","Z"],
            sidebarShow:false
        }
    },
    methods: {
        // 显示 侧滑菜单 顺便传ID
      showSide(id){
        let _self = this
        this.sidebarShow = true;
        _self.$nextTick(function(){
             _self.$eventBus.$emit("car",id)
        })
      },
      // 接收 Sidebar组件的返回值 隐藏 侧滑菜单
      setSidebar(val){
        this.sidebarShow = val;
      }
    },
    components:{
        msidebar
    }
};
</script>
<style lang="scss">
.van-index-bar{
    .van-cell{
        height: 50px;
        .van-cell__title{
