<template>
  <div class="selectCar">
    <!-- 标题 -->
    <van-nav-bar title="汽车报价大全" :fixed="true"/>

    <!-- 搜索框 -->
    <router-link to="/search">
      <van-search placeholder="请输入搜索关键词" shape="round" background="#ffffff" disabled/>
    </router-link>
    

    <!-- 条件选车、上市新车、新能源 -->
    <van-grid :column-num="3" icon-size="40px" :border="false">
      <van-grid-item icon="http://res.app.yiche.com/wechatapp/ic_sy_tjxc@2x.png" text="条件选车" to="/selectCar/screenCar"/>
      <van-grid-item icon="http://res.app.yiche.com/wechatapp/ic_ssxc@3x.png" text="上市新车"/>
      <van-grid-item icon="http://res.app.yiche.com/wechatapp/ic_sy_xny@2x.png" text="新能源"/>
    </van-grid>

    <!-- 热门 -->
    <p class="remen">热门</p>
    <van-grid :column-num="5" icon-size="40px" :border="false">
      <van-grid-item
        v-for="(item,index) in table"
        :key="index"
        :icon="item.CoverPhoto"
        :text="item.Name"
        @click="showSide(item.MasterID)"
      ></van-grid-item>
    </van-grid>

    <!-- 侧滑菜单 -->
    <msidebar :sidebarShow="sidebarShow" @hideSidebar="setSidebar"></msidebar>

    <!-- 汽车索引栏 -->
    <indexbar :carList="carList"></indexbar>

    <!-- 底部导航 -->
    <tabbar></tabbar>
  </div>
</template>
<script>
import tabbar from "../../components/tabBar";
import indexbar from "./IndexBar";
import msidebar from "./sidebar";
import * as api from "../../api/index.js";
export default {
  data() {
    return {
      carList: [],
      table: "",
      sidebarShow:false
    };
  },
  created() {
    this.getCarList();
  },
  methods: {
    async getCarList() {
      let { carList, table } = await api.getSelectCarInfo();
      (this.carList = carList),
        (this.table = table);
    },
      // 显示 侧滑菜单 顺便传ID
      showSide(id){
        let _self = this
        this.sidebarShow = true;
        _self.$nextTick(function(){
             _self.$eventBus.$emit("car",id)
        })
      },
      // 接收 Sidebar组件的返回值 隐藏 侧滑菜单
      setSidebar(val){
        this.sidebarShow = val;
      }
  },
  components: {
    tabbar,
    indexbar,
    msidebar
  }
};
</script>
<style lang="scss">
.selectCar {
  width: 100%;
  height: 100%;
  background: #f5f5f5 !important;
  padding: 50px 0;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .van-search {
    background: #f5f5f5 !important;
    .van-search__content {
      background: #fff;
    }
  }
  .van-grid {
    .van-grid-item__text {
      font-size: 14px;
    }
  }
  .remen {
    color: #888;
    padding: 5px 0 5px 15px;
    background: #f5f5f5;
    box-sizing: border-box;
    font-size: 14px;
  }
}
</style>